.social-links {
  text-decoration: none;
  color: aliceblue;
  align-items: flex-end;
  opacity: 1.0;
}

.social-links:hover {
  opacity: 0.8;
  transform: scale(1.3);
  transition: 0.5s;
  color: aliceblue;
}

.social-links:not(:hover) {
  transition: 0.5s;
  transform: scale(1.0);
}

.Contact {
  margin-right: 50px;
}

#root {
  width: 100vw;
}
